import { initReactI18next } from 'react-i18next';
import languageResources from './locales';
import i18next from 'i18next';

i18next
  .use(initReactI18next)
  .init({
    compatibilityJSON: "v3",
    lng: "vi",
    fallbackLng: 'en',
    debug: true,
    resources: languageResources
  });

export default i18next;